<template>
	<div class="groupChat">
		<header-view :headerObj="{title:'发起群聊',path:'/groupChat',count:0}"></header-view>
		<div class="compent"><component :is="currentView" @toLocal="showDefault"></component></div>
		<div v-show="defaultShow">
			<div class="groupChat-head">
				<div class="seach">
					<img src="../../../../static/img/search.png" alt="">
					<span>搜索</span>
				</div>
			</div>
			<ul class="contact">
				<li class="item-title" @click="pageChange('group')">
					<img src="../../../../static/img/icon/group.png" alt=""/>
					<span>选择已有的群</span>
				</li>
				<li class="item-title" @click="pageChange('friends')">
					<img src="../../../../static/img/icon/friend.png" alt=""/>
					<span>我的好友</span>
				</li>
				<li class="item-title" @click="pageChange('phone_contacts')">
					<img src="../../../../static/img/icon/phone1.png" alt=""/>
					<span>手机通讯录</span>
				</li>
			</ul>
			<div class="frequent-contacts">
				<p class="item-title" @click="expandCompany()">
					<img src="../../../../static/img/icon/jiagou.png" alt=""/>
					<span>深圳市智慧建设科技有限公司</span>
					<img v-bind:class="{arrow:companyShow}" src="../../../../static/img/icon/next-arrow.png" alt=""/>
				</p>
				<ul v-show="companyShow">
					<li  @click="pageChange('companyContacts')">
						<img src="../../../../static/img/icon/zhejiao.png" alt="">
						<span>组织架构</span>
					</li>
					<li>
						<img src="../../../../static/img/icon/zhejiao.png" alt="">
						<span>外部联系人</span>
					</li>
				</ul>
			</div>
			<div class="frequent-contacts">
				<p class="item-title" @click="expand()">
					<img src="../../../../static/img/call.png" alt=""/>
					<span>常用联系人</span>
					<img v-bind:class="{arrow:willShow}" src="../../../../static/img/icon/next-arrow.png" alt=""/>
				</p>
				<all-contacts v-show="willShow"></all-contacts>
			</div>
		</div>
		<footer>
 			<span>已选择:{{contactsSelected.length-1}}人</span><button @click="jump">确定({{contactsSelected.length-1}}/999)</button>
		</footer>
	</div>
</template>
<script>
	import friends from './friends'
	import group from './is_group'
	import phone_contacts from './phone_contacts'
	import HeaderView from '../../../components/appStore/header-x'
	import AllContacts from './all_contacts'
	import {mapState} from 'vuex'
	import companyContacts from './company_contacts'
	export default{
		data:function(){
			return{
				defaultShow:true,
				currentView:"",
				willShow:true,  //常用联系人显示
				companyShow:false,
				checked:false,
				items:[
					{imgSrc:"../../../../static/img/call.png",text:"tango"},
					{imgSrc:"../../../../static/img/call.png",text:"jack"},
					{imgSrc:"../../../../static/img/call.png",text:"tom"},
				],
				value:[]
			}

		},
		computed:{
			...mapState(["contactsSelected"])
		},
		components:{
	      friends,
	      group,
	      phone_contacts,
				HeaderView,
				AllContacts,
				companyContacts
	    },
		methods: {
			showDefault:function(msg){
				this.defaultShow = true;
				this.currentView="";
			},
			pageChange:function(url){
		    // console.log(url);
		    this.currentView=url;
		    this.defaultShow = false;
		  },
			expand:function(){
				//常用联系人列表展开控制
			    this.willShow=!this.willShow;
			},
			expandCompany(){
				this.companyShow=!this.companyShow
			},
			jump(){
				console.log(this.contactsSelected);
				// 如果只有一个人 直接跳转聊天页面  待完善
				if(this.contactsSelected===1){
					//……
				}else{
					this.$router.push({
						path:'/creatGroupMSg'
					})
				}

			}
		}
	}

</script>
<style lang='less' scoped>

	.groupChat{
		width:100%;
		height:100%;
		background:#EFEFF4;
		font-size: 16px;
		padding-top: 2rem;
		padding-bottom: 3rem;
		.seach{
			width:94%;
			height:1.5rem;
			margin:0.5rem auto;
			background:#fff;
			border-radius:3px;
			text-align:center;
			line-height:1.5rem;
			color:#858E99;
			display:flex;
      justify-content:center;
      align-items:center;
			img{
				width:15px;
				margin-right:10px;
			}
		}

		.contact,.frequent-contacts{
			background-color: #fff;

			.item-title{
				padding-left: 0.5rem;
				height:2.5rem;
				width: 100%;
				line-height: 2.5rem;
				display: flex;
				align-items: center;
				border-bottom:1px solid  #EAEAEA;
				span{
					margin-left: 0.5rem;
					flex: 1;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
			img{
				height: 1.5rem;
				border-radius: 50%;
			}
		}
		.frequent-contacts{
			/*常用联系人*/
			margin-top: 1rem;
			width: 100%;
			p{
				img:last-child{
					/*箭头*/
					height: 1rem;
					padding-right: 0.5rem;
				}
			}
			>ul{
				padding-left: 1rem;
				>li{
					border-bottom:1px solid  #EAEAEA;
					height: 2.5rem;
					line-height: 2.5rem;
					display: flex;
					align-items:center;
					>span{
						padding-left: .5rem;
					}
				}
			}
		}
		footer{
			position: fixed;
			display: flex;
			align-items: center;
			justify-content: space-between;
			bottom: 0;
			height: 2.5rem;
			width: 100%;
			padding: 0 0.5rem;
			background-color: #fAfAfA;
			span{
				border-radius: 5px;
				display: inline-block;
				color: #32acff;
				font-size: 13px;
			}
			button{
				height: 1.5rem;
				border: 0;
				border-radius: 5px;
				color: #fff;
				font-size: 13px;
				background-color: #8CE5FB;
			}

		}

	}
	.arrow{
		/*向右箭头转向下*/
		margin-right: 0.5rem;
		transform: translateY(0.3rem) rotate(90deg);
	}

	/*input样式修改*/
	.comList_checkbox {
		opacity: 0;
	}
	.comList_forCheckbox{
		width: 20px;
		height: 20px;
		color: #fff;
		cursor: pointer;
		text-align: center;
		font: bold 20px/100% '华文中宋';
		display: inline-block;
		border-radius: 50%;
		background-color: #fff;
		border:solid 1px #E0E0E0;
	}

</style>



