<template>
	<div class="group-page">
		<div class="group-title">
			<span class="title" @click="goBack">联系人</span>
			<img src="../../../../static/img/icon/next-arrow.png" />
			<span>我的群组</span>
		</div>
		<div class="content">
			<ul class="group-list">
				<li v-for="item in items">
					<img class="group-img" :src="item.imgSrc" />
					<div class="group-message">
						<span class="group-name">{{item.name}}</span>
						<span class="group-number">{{item.number}}人</span>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				items: [{
						imgSrc: "../../../../static/img/call.png",
						name: "vue交流群",
						number: 10
					},
					{
						imgSrc: "../../../../static/img/call.png",
						name: "吃货群",
						number: 20
					},
					{
						imgSrc: "../../../../static/img/call.png",
						name: "tom",
						number: 30
					},
				],
			}
		},
		methods: {
			goBack: function() {
				this.$emit('toLocal', 'back to parent'); //调用父组件change事件
			},
		}
	}
</script>

<style lang='less' scoped>
	.group-page {
		.group-title {
			display: flex;
			align-items: center;
			padding-left: 0.5rem;
			height: 2rem;
			background-color: #fff;
			border-bottom: solid 1px #E4E4E4;
			span{
				display: inline-block;
				color: #32ACFF;
				&:last-child{
					color: #808080;
				}
			}
			img {
				height: 0.7rem;
				margin: 0 .5rem;
			}
		}
		.group-list {
			min-height: 10rem;
			>li{
				padding:.5rem;
				border-bottom: 1px solid #eee;
				>img{
					float: left;
					height: 2rem;
					width: 2rem;
				}
				.group-message {
					margin-left: 2.5rem;
					span:last-child{
						display: block;
						color:#aaa;
						font-size: 12px;
						padding-top:5px;
					}
				}
			}
		}
	}
</style>



