<template>
	<ul class="contacts-list">
		<li v-for="item in contactsMsg.commonContacts" :class="{active:isActive(item)}" @click="toggleActive($event,item)">
      <table>
        <tr>
          <td>
            <span><i class="icon-ok"></i></span>
          </td>
          <td>
            <img v-if="item.imgSrc" :src="item.imgSrc" alt="">
            <p v-else class="imgDiv">{{item.name.substr(-2,2)}}</p>
          </td>
          <td>
            <div>
              <p>{{item.name}}</p>
              <p v-if="item.title" class="title">{{item.title}}</p>
            </div>
          </td>
        </tr>
      </table>

			<div>



			</div>
		</li>
	</ul>
</template>

<script>
  import {mapState,mapMutations} from "vuex"
	export default {
    name:'allContacts',
    computed:{
      ...mapState(["contactsMsg","contactsSelected"])
    },
		methods: {
			toggleActive(event,item){
				$(event.currentTarget).toggleClass("active")
				this.toggleSelectedContacts(item)
			},
			...mapMutations(["toggleSelectedContacts"]),
			isActive(item){
				// var result=this.contactsSelected.indexOf(item)
				// console.log("是否存在",result);
				// if(result===-1){return false}else{return true}
				for (var i = 0; i < this.contactsSelected.length; i++) {
					if(item.id==this.contactsSelected[i].id){
						return true
					}
				}
				return false
			}
		}
	}
</script>

<style lang="less" scoped>
	.contacts-list {
		background-color: #fff;
		border-bottom: solid 1px #EAEAEA;
		>li {
			padding-left: 0.5rem;
			height: 2.5rem;
			border-bottom: solid 1px #EAEAEA;
			&.active{
				span{background-color:#26a2ff;}
			}
      tr{
        td{
          height: 2.5rem;
          >span {
            display: inline-block;
            vertical-align:middle;
            margin-right: .7rem;
            width: 20px;
            height: 20px;
            border-radius:50%;
            border:1px solid #aaa;
            position:relative;
            >i{
              font-size:14px;
              position:absolute;
              left:3px;
              top:3px;
              color:#fff;
            }
          }
          >img{
              vertical-align:middle;
              width:1.5rem;
              height:1.5rem;
            }
          >.imgDiv{
              display:inline-block;
              width:1.5rem;
              height:1.5rem;
              line-height: 1.5rem;
              border-radius:50%;
              background:#26a2ff;
              text-align: center;
              vertical-align:middle;
              color:#fff;
              font-size:12px;
            }
          >div{
              display:inline-block;
              padding-left:.5rem;
              >p{
                font-size:14px;
                line-height:1rem;
                &.title{
                  font-size:12px;
                  color:#999;
                  /*padding-top:5px;*/
                }
              }
            }
        }
      }
		}
	}
</style>



