<template>
	<div>
		<div class="group-title">
			<span @click="goBack">联系人</span>
			<img src="../../../../static/img/icon/next-arrow.png" />
			<span>我的好友</span>
		</div>
		<all-contacts></all-contacts>
	</div>
</template>

<script>
	import AllContacts from './all_contacts'
	export default {
		data: function() {
			return {
				isActive:false,
				items:[
					{imgSrc:"../../../../static/img/call.png",text:"tango"},
					{imgSrc:"../../../../static/img/call.png",text:"jack"},
					{imgSrc:"../../../../static/img/call.png",text:"tom"},
				],
			}
		},
		components:{
			AllContacts
		},
		methods: {
			goBack: function() {
				this.$emit('toLocal', 'back to parent'); //调用父组件change事件
			}
		}
	}
</script>

<style lang="less" scoped>

	.group-title{
		display: flex;
		align-items: center;
		padding-left: 0.5rem;
		height: 2rem;
		background-color: #fff;
		border-bottom:solid 1px #E4E4E4;
		span{
			display: inline-block;
			color: #32ACFF;
			&:last-child{
				color: #808080;
			}
		}
		img{
			height: 1rem;
			margin:0 0.5rem;
		}
	}
</style>



