<template>
	<div>
		<div class="group-title">
			<span @click="goBack();">联系人</span>
			<img src="../../../../static/img/icon/next-arrow.png" />
			<span>手机通讯录</span>
		</div>
		<div class="content">
			<ul class="group-list">
				<li v-for="item in items" @click="toggleActive($event)">
					<span><i class="icon-ok"></i></span>
					{{item.text}}
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				items:[
					{imgSrc:"../../../../static/img/call.png",text:"tango"},
					{imgSrc:"../../../../static/img/call.png",text:"jack"},
					{imgSrc:"../../../../static/img/call.png",text:"tom"},
				],
			}
		},
		methods: {
			goBack: function() {
				console.log('===toLocal');
				this.$emit('toLocal', 'back to parent'); //调用父组件change事件
			},
			toggleActive(event){
				$(event.currentTarget).toggleClass("active")
			}
		}
	}
</script>

<style lang="less" scoped>

	.group-title{
		display: flex;
		align-items: center;
		padding-left: 0.5rem;
		height: 2rem;
		background-color: #fff;
		border-bottom:solid 1px #E4E4E4;
		span{
			display: inline-block;
			color: #32ACFF;
			&:last-child{
				color: #808080;
			}
		}
		img{
			height: 1rem;
			margin:0  0.5rem;
		}
	}
	.group-list {
		background-color: #fff;
		border-bottom: solid 1px #EAEAEA;
		>li {
			padding-left: 0.5rem;
			height: 2.5rem;
			line-height: 2.5rem;
			border-bottom: solid 1px #EAEAEA;
			display: flex;
			align-items: center;
			&.active{
				>span{background-color:#26a2ff;}
			}
			>span {
				display: inline-block;
				margin-right: .7rem;
				width: 20px;
				height: 20px;
				border-radius:50%;
				border:1px solid #aaa;
				position:relative;
				>i{
					font-size:14px;
					position:absolute;
					left:3px;
					top:3px;
					color:#fff;
				}
			}
		}
	}


</style>



