<template>
	<div>
		<div class="group-title">
			<span @click="goBack">联系人</span>
			<img src="../../../../static/img/icon/next-arrow.png" />
			<span>深圳市智慧建设科技有限公司</span>
		</div>
    <ul class="contacts-list">
  		<li v-for="item in contactsMsg.companyContacts" :class="{active:isActive(item)}" @click="toggleActive($event,item)">
        <table>
          <tr>
            <td>
              <span><i class="icon-ok"></i></span>
            </td>
            <td>
              <img v-if="item.imgSrc" :src="item.imgSrc" alt="">
              <p v-else class="imgDiv">{{item.name.substr(-1,2)}}</p>
            </td>
            <td>
              <div>
                <p>{{item.name}}</p>
                <p v-if="item.title" class="title">{{item.title}}</p>
              </div>
            </td>
          </tr>
        </table>

  			<div>



  			</div>
  		</li>
  	</ul>
	</div>
</template>

<script>
	import {mapState,mapMutations} from "vuex"
	export default {
    name:"companyContacts",
    computed:{
      ...mapState(["contactsMsg","contactsSelected"])
    },
		methods: {
      toggleActive(event,item){
        $(event.currentTarget).toggleClass("active")
        this.toggleSelectedContacts(item)
      },
      ...mapMutations(["toggleSelectedContacts"]),
      isActive(item){
        for (var i = 0; i < this.contactsSelected.length; i++) {
          if(item.id==this.contactsSelected[i].id){
            return true
          }
        }
        return false
        // var result=this.contactsSelected.indexOf(item)
        // // console.log(this.contactsSelected);
        // // console.log(item);
        // // console.log(result);
        // console.log(item.id==this.contactsSelected[0].id);
        // if(result===-1){return false}else{return true}
      },
			goBack: function() {
				this.$emit('toLocal', 'back to parent'); //调用父组件change事件
			}
		}
	}
</script>

<style lang="less" scoped>

	.group-title{
		display: flex;
		align-items: center;
		padding-left: 0.5rem;
		height: 2rem;
		background-color: #fff;
		border-bottom:solid 1px #E4E4E4;
		span{
			display: inline-block;
			color: #32ACFF;
      min-width:50px;
			&:last-child{
				color: #808080;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width:200px;
			}
		}
		img{
			height: 1rem;
			margin:0 0.5rem;
		}
	}
  .contacts-list {
		background-color: #fff;
		border-bottom: solid 1px #EAEAEA;
		>li {
			padding-left: 0.5rem;
			height: 2.5rem;
			border-bottom: solid 1px #EAEAEA;
			&.active{
				span{background-color:#26a2ff;}
			}
      tr{
        td{
          height: 2.5rem;
          >span {
            display: inline-block;
            vertical-align:middle;
            margin-right: .7rem;
            width: 20px;
            height: 20px;
            border-radius:50%;
            border:1px solid #aaa;
            position:relative;
            >i{
              font-size:14px;
              position:absolute;
              left:3px;
              top:3px;
              color:#fff;
            }
          }
          >img{
              vertical-align:middle;
              width:1.5rem;
              height:1.5rem;
            }
          >.imgDiv{
              display:inline-block;
              width:1.5rem;
              height:1.5rem;
              line-height: 1.5rem;
              border-radius:50%;
              background:#26a2ff;
              text-align: center;
              vertical-align:middle;
              color:#fff;
              font-size:12px;
            }
          >div{
              display:inline-block;
              padding-left:.5rem;
              >p{
                font-size:14px;
                line-height:1rem;
                &.title{
                  font-size:12px;
                  color:#999;
                  /*padding-top:5px;*/
                }
              }
            }
        }
      }
		}
	}
</style>



